// Styling 
import "../App.css";
// Dependencies 
import axios from "axios";
import $ from "jquery";
import React, { Component, useEffect, useState } from "react";
import MaterialTable from 'material-table';
import { withTranslation, Trans } from 'react-i18next';
import { Grid } from "@material-ui/core";

function Resources(props) {
    const [resources, setResources] = useState();
    const [userUploads, setUserUploads] = useState();

    useEffect(()=> {
        getFiles();
    },[])


    function getFiles()  {
        axios.get("http://localhost:3000/api/uploads/getUserUploads")
            .then(res => {
                setUserUploads(res.data);
            })
            .catch(err => console.log(err));
    }

    function deleteResources(doc_id, resolve){
        axios.get("http://localhost:3000/api/uploads/deleteUserUpload/"+doc_id)
        .then(res => {
            console.log(res.data)
            resolve()
            window.location.reload();
            
        })
        .catch(err => console.log(err));       
    }

    //Call to action methods
    function onBackClick() {
        props.history.push("/admin");
    };

    const {t, i18n} = props;
    console.log(resources)
    return (
        <div className="UploadedDocs">
             <header>
                    <h1 id="title-pages">
                        Document Déposer
                    </h1>
                    <div className="container">
                        <div className="row">
                            <div id="back-btn" className="col offset-s1 offset-m1">
                                <button onClick={onBackClick} style={{ 
                                    width: "125px",
                                    top:"27px",
                                    left:"25px",
                                    borderRadius: "3px",
                                    letterSpacing: "1.5px",
                                    marginTop: "1rem"}}
                                    className="btn btn-large waves-effect waves-dark hoverable white black-text accent-3">
                                    {<Trans>{t("back")}</Trans>}
                                </button>
                            </div>
                        </div>
                    </div> 
                </header>
                    <Grid
                    container
                    direction="row"
                    justifyContent="center"
                    alignItems="center"
                    >
                        <Grid item xs={10}>
                            <MaterialTable
                                
                                title={<Trans>{t("availResBtn")}</Trans>} 
                                actions={[
                                    {
                                      icon: 'refresh',
                                      tooltip: 'Refresh Data',
                                      isFreeAction: true,
                                      onClick: () => window.location.reload(),
                                    }
                                  ]}
                                columns={
                                    [
                                        {title: <Trans>{t("name")}</Trans>, field: 'file_name'},
                                        {title: <Trans>{t("category")}</Trans>, field: 'category'},
                                        {title: <Trans>{t("user")}</Trans>, field: 'User.name'},
                                        {title: <Trans>{t("client")}</Trans>, field: 'Client.name'},
                                        {title: <Trans>{t("date")}</Trans>, field: 'createdAt'}
                                        
                                        // {title: 'file', field:'file'}
                                    ]
                                }
                                data={userUploads}
                                editable={{
                                    onRowDelete: (oldData) => 
                                        new Promise((resolve) => {
                                            /// function to delete
                                            deleteResources(oldData.id, resolve)
                                        }),
                                }}
                                options={{
                                    exportButton: true
                                  }}
                                detailPanel={[
                                    {tooltip: <Trans>{t("viewDoc")}</Trans>,
                                        render: rowData => {
                                            return (
                                              <iframe
                                                width="100%"
                                                height="600px"
                                                src={"https://api.forumconcepts.fr/UserUploads/"+rowData.file_name}
                                                frameborder="0"
                                                allowfullscreen
                                              />
                                            )
                                          }
                                    }
                                ]}
                            />         
                        </Grid>
                    </Grid>
                            
                        
                    
        </div>
    )
}

export default (withTranslation("translations")(Resources));

