    //Generates code for selected HUB Menu option
    HubcheckboxChange = e => {
      var item = e.target.value;
      if(e.target.checked){
        
        this.setState({
        selectedopts: this.state.selectedopts.set(item, e.target.checked),
        });
        
        }
        else if(!e.target.checked) {
          this.setState({
            selectedopts: this.state.selectedopts.set(item, e.target.checked)
            });
          }
        let obj = Array.from(this.state.selectedopts).reduce((obj, [key, value]) => {
            obj[key] = value;
            return obj;
          }, {});
          //console.log(obj)
          
          let optsraw = JSON.stringify(Object.keys(obj));
          this.state.hubOpts = optsraw.replace(/[, ""]/g, "");
          console.log(this.state.hubOpts);  
      }

      //PUT code generated for HUB Menu option for selected user
      hubgenCode = () => {
        const hubOpts = { hubOpts: this.state.hubOpts}
        axios.put("https://api.forumconcepts.fr/api/updateuser/"+this.state.UserSelectedData._id, hubOpts)
      .then(res => {
        console.log(this.state.hubOpts);
        console.log(res.data);
      })
      .catch(err => console.log(err));
      }


