// Styling 
import "../App.css";
// Dependencies 
import axios from "axios";
import $ from "jquery";
import React, { Component, useEffect, useState } from "react";
import MaterialTable from 'material-table';
import { withTranslation, Trans } from 'react-i18next';

function Resources(props) {
    const [resources, setResources] = useState();
    
    useEffect(()=> {
        getFiles();
    },[])


    function getFiles()  {  
        axios.get("http://localhost:3000/api/files/getResources")
            .then(res => {
                    setResources(res.data);
            })
    }

    function deleteResources(doc_id, resolve){
        axios.get("http://localhost:3000/api/files/deleteResource/"+doc_id)
        .then(res => {
            console.log(res.data)
            resolve()
            window.location.reload();
            
        })
        .catch(err => console.log(err));
        
    }

    //Call to action methods
    function onBackClick() {
        props.history.push("/admin");
    };

    const {t, i18n} = props;
    console.log(resources)
    return (
        <div className="Resources">
             <header>
                    <h1 id="title-pages">
                        <Trans>{t("availResBtn")}</Trans>
                    </h1>
                    <div className="container">
                        <div className="row">
                            <div id="back-btn" className="col offset-s1 offset-m1">
                                <button onClick={onBackClick} style={{ 
                            width: "125px",
                            borderRadius: "3px",
                            letterSpacing: "1.5px",
                            marginTop: "1rem"}}
                            className="btn btn-large waves-effect waves-dark hoverable white black-text accent-3">
                                    <Trans>{t("back")}</Trans>
                                </button>
                            </div>
                        </div>
                    </div> 
                </header>
                <div className="container">
                    <div className="row">
                        <div className="col s12 m12">
                            <MaterialTable 
                                title={<Trans>{t("availResBtn")}</Trans>} 
                                actions={[
                                    {
                                      icon: 'refresh',
                                      tooltip: 'Refresh Data',
                                      isFreeAction: true,
                                      onClick: () => window.location.reload(),
                                    }
                                  ]}
                                columns={
                                    [
                                        {title: <Trans>{t("name")}</Trans>, field: 'file_name'},
                                        {title: <Trans>{t("category")}</Trans>, field: 'category'},
                                        {title: <Trans>{t("client")}</Trans>, field: 'Client.name'},
                                        // {title: 'file', field:'file'}
                                    ]
                                }
                                data={resources}
                                editable={{
                                    onRowDelete: (oldData) => 
                                        new Promise((resolve) => {
                                            /// function to delete
                                            deleteResources(oldData.id, resolve)
                                        }),
                                }}
                                detailPanel={[
                                    {tooltip: <Trans>{t("viewDoc")}</Trans>,
                                        render: rowData => {
                                            return (
                                              <iframe
                                                width="100%"
                                                height="600px"
                                                src={"https://api.forumconcepts.fr/Resources/"+rowData.file_name}
                                                frameborder="0"
                                                allowfullscreen
                                              />
                                            )
                                          }
                                    }
                                ]}
                                />
                        </div>  
                    </div>
                </div>
        </div>
    )
}

export default (withTranslation("translations")(Resources));

